import { useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Table } from "antd";
import Breadcrumbs from "../../../components/Breadcrumbs"
import { useFetchByLoad } from "../../../contexts/useFetchByLoad";
import ListTitle from "../../../components/ListTitle";
const resource = "system_users_groups";

export default function UserGroups() {
	let timer = useRef<any>(null)
	const navigate = useNavigate();

	const [query, setQuery] = useState({ "skip": 0, "take": 10, search: '' })
	const { fetch, data, loading } = useFetchByLoad();

	useEffect(() => {
		clearTimeout(timer.current)
		timer.current = setTimeout(() => {
			fetch({ url: resource, query: JSON.stringify(query) })
		}, 500)
	}, [query])

	const columns = [
		{
			title: "User Group Name",
			dataIndex: "systemUserGroupName",
			sorter: true,
		},
		{
			title: "Apps Allowed",
			dataIndex: "omsFormStep1",
			sorter: true,
		},
		{
			title: "Actions",
			dataIndex: "actions",
			key: "actions",
			render: (_value: any, record: any) => (
				<div className="text-center actions">
					<a href="users-groups.php?action=edit&amp;id=3" title="Edit Sales Team [Type 3]"><img className="navigicon" src="assets/apps/images/edit-dark-grey.png" /></a>&nbsp;&nbsp;
					<a href="javascript:void(0);" title="Delete Sales Team [Type 3]"><img className="navigicon" src="assets/apps/images/lock-dark-grey.png" /></a>&nbsp;&nbsp;
					<a href="#" id="log3" className="recloginfo"><img className="navigicon" src="assets/apps/images/info-dark-grey.png" /></a>
				</div>
			),
		},
	];

	return (
		<>
			<Breadcrumbs root="" pageName="Users Groups" />

			<div className="main-content-body">
				<div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
					<div className="row">
						<div className="card">

							<ListTitle ListName="List All Users group" />

							<div className="table-responsive">
								<div className="card-body">
									<div className="dataTables_wrapper form-inline dt-bootstrap no-footer">
										<div className="clear"></div>
										<div className="col-lg-6 col-md-6 col-sm-6 col-xs-12 no-padding dataTables_length">
											<label>
												<select name="users-groups_length" aria-controls="users-groups" className="fl form-control input-sm"
													value={query.take} onChange={(obj) => setQuery({ ...query, "take": Number(obj.target.value) })}>
													<option value="10">10</option>
													<option value="20">20</option>
													<option value="25">25</option>
													<option value="50">50</option>
													<option value="100">100</option>
												</select>
												<div className="fl rec">records per page.</div>
											</label>
										</div>
										<div className="dataTables_filter">
											<label>Search Record: <input type="search" className="form-control input-sm" placeholder="" value={query.search} onChange={(obj) => setQuery({ ...query, "search": obj.target.value })} /></label>
										</div>
									</div>

									<Table className="mainTable" loading={loading} dataSource={data?.data ?? []} columns={columns} pagination={{
										showQuickJumper: true,
										showTotal: (total) => `Showing ${total} records.`,
										total: data?.count ?? 0,
										onChange: (page, pageSize) => {
											setQuery({ ...query, "skip": ((page - 1) * pageSize), "take": pageSize });
										},
									}} />
								</div>
								<div className="card-action table-buttons">
									<div className="fl">
										<button type="button" name="Groups_Delete_Selected" className="btn btn-danger delete btn-raised text-uppercase ripple" title="Delete Selected">Delete Selected</button>&nbsp;&nbsp;&nbsp;
									</div>
									<div className="fl">
										<button type="button" onClick={() => navigate('/add-user-group')} name="Groups_Add_New" className="btn btn-success add btn-raised text-uppercase ripple" title="Add New Group">Add New Group</button>
									</div>
								</div>
								<div className="clear"></div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</>
	);
}


